* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
    'Lucida Sans', Arial, sans-serif;
}

body {
  background: 
  linear-gradient(90deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px),
  linear-gradient(#eee .1em, transparent .1em);
  background-size: 100% 1.2em;
}

.todo-header {
  margin: 38px;
  color: rgb(88, 88, 88);
  font-size: 24px;
}

.todo-app {
  display: flex;
  flex-direction: column;
  position:absolute;
  min-height: 90vh;
  height: auto;
  background: rgba(117, 117, 117, 0.233);
  text-align: center;
  border-radius: 10px;
  padding-bottom: 32px;
  width: 94%;
  margin: 3%;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 24px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid rgb(161, 161, 161);
  border-width: 3px;
  width: 87%;
  background: transparent;
  color: rgb(0, 0, 0);
}

.todo-input::placeholder {
  color: #0000005e;
}

.todo-button {
  padding: 12px 15px 15px ;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  font-size: large;
  text-transform: capitalize;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 2px auto;
  color: rgb(61, 61, 61);
  padding: 15px;
  border-radius: 5px;
  width: 90%;
}

.todo-row .todo-date {
  margin-bottom: -1.5em;
  padding: 1em;
  font-size:x-small;
}
.todo-row .todo-text {
  margin-left: 13%;
  width: 75%;
}

.todo-row:nth-child(2n + 1) {
  background: rgba(24, 21, 21, 0.144)
}

.todo-row:nth-child(2n + 2) {
  background: rgba(32, 87, 124, 0.377)
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: rgb(0, 0, 0);
}

.edit-icon {
  color: rgb(0, 0, 0);
}

.add-icon {
  margin-bottom: -20px;
  padding: 14px;
  height: 50px;
  width: 50px;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  background: rgb(161, 161, 161);
}

.update-icon {
  margin-bottom: -20px;
  padding: 15px;
  height: 49px;
  width: 50px;
  color: rgb(88, 88, 88);
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: rgb(161, 161, 161);

}

@media (max-width: 400px){
  body {
    height: auto;
    overflow-x: hidden !important;
  }
  .header {
    font-size: 10px;
  }
  .todo-app {
    margin: 3% 3% 3% 3%;
    margin-bottom: 2vh;
    min-height: 96vh;
    width: 94%;
  }
  .todo-input {
    width: 80%;
  }
  .todo-row {
    width: 90%;
  }
}